<template>
  <van-overlay :show="show" z-index="99999" class="landscape-mask">
    <van-image fit="fill" :src="imgData" alt="图片加载失败" />
    <span>为了您的良好体验</span>
    <span>请将手机/平板竖屏操作</span>
  </van-overlay>
</template>

<script>
export default {
  name: 'LandscapeMask',
  data() {
    return {
      show: false,
      imgData:
        'data:image/png;base64,'
    }
  },
  mounted() {
    // 监听 resize 方法
    window.addEventListener('resize', this.renderResize, false)
  },
  methods: {
    // 判断横竖屏,并添加横屏提示页面
    renderResize() {
      // 判断横竖屏
      let width = document.documentElement.clientWidth
      let height = document.documentElement.clientHeight
      if (width > height) {
        this.show = true
      } else {
        this.show = false
      }
    }
  },
  created() {
    this.renderResize()
  }
}
</script>

<style lang="less">
.landscape-mask {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #2e2e2e;
  img {
    width: 50px;
    height: 60px;
    margin-bottom: 10px;
  }
  span {
    width: 100%;
    height: auto;
    font-size: 13px;
    color: #626262;
    line-height: 13px;
    margin-bottom: 3px;
    text-align: center;
  }
}
</style>
